import { mapState, mapMutations } from 'vuex'
import { upyunUrl } from '../../api/config'

export default {
  data () {
    return {
      upyunUrl
    }
  },
  onLoad () {
  },
  computed: {
    ...mapState([
    ])
  },
  methods: {
    ...mapMutations({
    }),
    onSkipPageFn (item) {
      let url = ''

      switch (item.type) {
        case 0:
          url = `/pages/robTuition/tuitionBill/index?oJson=${encodeURIComponent(JSON.stringify(item))}`
          break
        case 1:
          url = `/pages/robTuition/tuitionShare/index?oJson=${encodeURIComponent(JSON.stringify(item))}`
          break
        case 2:
          url = `/pages/robTuition/tuitionShare/index?oJson=${encodeURIComponent(JSON.stringify(item))}`
          break
        default:
          return false
      }
      uni.navigateTo({
        url
      })
    },
    onShowStatusFn () {
      this.$emit('meListStatusChild', false)
    }
  },
  components: {
  },
  props: {
    lists: {
      type: Array,
      default: []
    },
    status: {
      type: Boolean,
      default: false
    }
  }
}
